define('frontend-cp/components/ko-file-size/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    tagName: 'span',
    size: null,

    options: (function () {
      var size = this.size;
      var options = undefined;
      if (size > 1024 * 1024) {
        options = {
          size: size / 1024 / 1024,
          unit: 'mb'
        };
      } else {
        options = {
          size: size / 1024,
          unit: 'kb'
        };
      }
      return options;
    }).property('size')
  });

});