define('frontend-cp/components/ko-radio/component', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    disabled: false,
    large: false,
    value: null,
    checked: null,
    ariaLive: 'assertive',
    tabindex: 0,
    label: '',

    selected: (function () {
      return this.value === this.checked;
    }).property('value', 'checked'),

    // https://github.com/terrawheat/ember-cli-keyboard-actions/issues/1

    keyDown: function keyDown(e) {
      if (e.keyCode === 32) {
        return false;
      }
    },

    keyUp: function keyUp(e) {
      if (e.keyCode === 32) {
        this.send('toggleRadio');
      }
      return false;
    },

    actions: {
      toggleRadio: function toggleRadio() {
        if (!this.disabled) {
          if (this.selected) {
            if (this.onRadio) {
              this.sendAction('onRadio', this.get('checked'));
            } else {
              this.set('checked', this.value);
            }
          }
        }
      }
    }

  });

});